<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
.companyType {
  .el-table--border,
  .el-table--group {
    margin-top: 20px;
  }
  .el-table th {
    padding: 8px 0px;
  }
  .el-table td {
    padding: 0;
  }
  .el-dialog__body {
    padding-top: 0;
  }
}
</style>

<template>
  <div class='companyType'>
    <div class='table'>
      <div class='title'>
        <span class='title-text'></span>
        <span class='search-block'>
         <span class='input-pad-right'>
            <el-input v-model="searchObj.search" class='query' placeholder=""></el-input>
          <el-button @click="getCompanyAll" icon="el-icon-search" circle class='margin-right'></el-button>
         </span>
          <el-button class='pos-btn' type="primary" @click='cancel'></el-button>
        </span>
        
      </div>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="70px"></el-table-column>
        <el-table-column prop="company_name" label="" align='center'></el-table-column>
        <el-table-column prop="absolute_address" label="" align='center' width="280px"></el-table-column>
        <el-table-column prop="project_hold_name" label="" align='center'></el-table-column>
        <el-table-column prop="project_hold_phone" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="begin_time" label="" align='center'></el-table-column>
        <el-table-column prop="end_time" label="" align='center'></el-table-column>
        <el-table-column prop="price" label="" align='center' v-if='companyType==0'></el-table-column>
        <el-table-column prop="price" label="" align='center' v-if='companyType==1'></el-table-column>
        <el-table-column prop="price" label="" align='center' v-if='companyType==2'></el-table-column>
        <el-table-column property="operation" label="" align='center'>
          <template slot-scope="scope">
            <el-button type="text" @click='showAdd(scope.row)'></el-button>
          </template>
        </el-table-column>
      </el-table>

    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      searchObj: {
        search: ""
      },
      companyType: 0, //0   1    2 
      project_id: ""
    };
  },
  mounted() {
    this.companyType = this.$route.params.companyType;
    this.project_id = this.$route.params.project_id;
    if (this.project_id) {
      this.getCompanyAll();
    } else {
      this.$router.push({ name: "commission" });
    }
  },
  methods: {
    async getCompanyAll() {
      if (this.companyType == 0) {
        let res = await this.api.getCompanyAll({
          project_id: this.project_id,
          search: this.searchObj.search
        });
        if (res.code == 200) {
          this.tableData = res.data.data;
        }
      } else if (this.companyType == 1) {
        let res = await this.api.getCompanyY({ project_id: this.project_id });
        if (res.code == 200) {
          this.tableData = res.data.data;
        }
      } else if (this.companyType == 2) {
        let res = await this.api.getCompanyNList({
          project_id: this.project_id
        });
        if (res.code == 200) {
          this.tableData = res.data.data;
        }
      }
    },
    cancel() {
      this.$router.push({
        name: "commission"
      });
    },
    async showAdd(row) {
      if (this.companyType == 0) {
        this.$router.push({
          name: "companyPriceInfo",
          params: {
            project_id: row.project_id,
            company_rule_id: row.company_rule_id,
            companyType: this.companyType
          }
        });
      } else if (this.companyType == 1) {
        this.$router.push({
          name: "companyPriceInfo",
          params: {
            company_id: row.company_id,
            project_id: row.project_id,
            companyType: this.companyType
          }
        });
      } else if (this.companyType == 2) {
        this.$router.push({
          name: "companyPriceInfo",
          params: {
            company_rule_id: row.company_rule_id,
            project_id: row.project_id,
            companyType: this.companyType
          }
        });
      }
    }
  }
};
</script>



